<template>
  <div ref="map" class="map">
    <button class="give-up" @click="endGame">Give Up</button>
  </div>
</template>

<script>
import countryBoundaries from './../assets/country_boundaries.js';
import pin from './../assets/pin.png';
import leaflet from 'leaflet';
import './../assets/leaflet.zoomcss.js';

export default {
  name: 'WorldMap',
  methods: {
    endGame: function () {
      if (!this.$store.state.gameOver) {
        for (var i = 0; i < this.$store.state.countries.length; i++) {
          if(this.$store.state.countries[i].independent) {
            if (!this.$store.state.countries[i].answeredCountry || !this.$store.state.countries[i].answeredCapital) {
              var that = this;

              function getName() {
                if (!that.$store.state.countries[i].answeredCountry && !that.$store.state.countries[i].answeredCapital) {
                  return that.$store.state.countries[i].name + ', ' + that.$store.state.countries[i].capital;
                } else if (!that.$store.state.countries[i].answeredCountry && that.$store.state.countries[i].answeredCapital) {
                  return that.$store.state.countries[i].name;
                } else {
                  return that.$store.state.countries[i].capital;
                }
              }

              var myIcon = leaflet.divIcon({ 
                iconSize: new L.Point(62, 4), 
                html: getName(),
                className: 'label'
              });
              leaflet.marker([this.$store.state.countries[i].latlng[0], this.$store.state.countries[i].latlng[1]], {icon: myIcon}).addTo(this.$store.state.leaflet)

              this.$store.commit('gameOver');
            }
          }
        }
      }
    }
  },
  mounted () {

    // --------------
    // Initialize map
    // --------------

    var worldMap = leaflet.map(this.$refs.map, {
      center: [43.64701, -79.39425],
      minZoom: 3,
      maxZoom: 6,
      zoom: 3,
      maxBoundsViscosity: 1,
      attributionControl: false
    });

    this.$store.commit('initializeMap', worldMap)

    // ---------------------------------------------
    // Add map tiles + prevent panning out of bounds
    // ---------------------------------------------

    leaflet.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Physical_Map/MapServer/tile/{z}/{y}/{x}').addTo(worldMap);
    // leaflet.tileLayer('https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_nolabels/{z}/{x}/{y}.png').addTo(worldMap);
    worldMap.setMaxBounds([[84.67351256610522, -198.0234375], [-58.995311187950925, 223.2421875]]);

    // ----------------------
    // Add country boundaries
    // ----------------------

    leaflet.geoJson(countryBoundaries, {
      style: function (feature, layer) {
        return {
          weight: 0.8,
          opacity: 0.2,
          color: 'black',
          fillOpacity: 0,
          className: feature.id,
        }
      }
    }).addTo(worldMap);

    // ----------------
    // Add country pins
    // ----------------

    var redIcon = leaflet.icon({
      iconUrl: pin,
      iconSize:     [9, 16], // size of the icon
      iconAnchor:   [4, 8], // point of the icon which will correspond to marker's location
      className: 'marker-countries'
    });

    var blueIcon = leaflet.icon({
      iconUrl: pin,
      iconSize:     [9, 16], // size of the icon
      iconAnchor:   [4, 8], // point of the icon which will correspond to marker's location
      className: 'marker-countries'
    });

    for (var i = 0; i < this.$store.state.countries.length; i++) {
      if(this.$store.state.countries[i].independent) {
        this.$store.state.countries[i].marker = leaflet.marker(
          [this.$store.state.countries[i].latlng[0], this.$store.state.countries[i].latlng[1]],
          {icon: redIcon}
        ).addTo(worldMap);
      }
    }

  } // mounted
} // export default

</script>
<style>
  .give-up {
    position: absolute;
    bottom: 20px;
    left: 20px;
    background: white;
    z-index: 500;
    padding: 10px 18px;
    font-size: 14px;
    font-weight: bold;
    border: 0;
    border-radius: 4px;
    cursor: pointer;
    border: 1px solid #959595;
  }

  .label {
    background: white;
    white-space: nowrap;
    width: auto !important;
    height: auto !important;
    margin-left: 10px !important;
    margin-top: -14px !important;
    padding: 2px 10px 2px 4px;
    opacity: 0.7;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
    transition: opacity 0.2s;
  }

  .label:hover {
    z-index: 1000 !important;
    opacity: 1;
  }

  .label:after {
    content: '';
    position: absolute;
    top: 0;
    right: 100%;
    width: 10px;
    height: 100%;
    border-left: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 10px solid white;
  }

  .map {
    flex-grow: 1;
  }

  .wave-test {
    background: url('./../assets/wave.png') 0 0 repeat-x;
    background-size: auto 100%;
    animation: wave 17s linear infinite;
    opacity: 0.3;
  }

  @keyframes wave {
    0% {background-position: 0 0;}
    100% {background-position: 250px 0;}
  }

  /* required styles */

.leaflet-pane,
.leaflet-tile,
.leaflet-marker-icon,
.leaflet-marker-shadow,
.leaflet-tile-container,
.leaflet-pane > svg,
.leaflet-pane > canvas,
.leaflet-zoom-box,
.leaflet-image-layer,
.leaflet-layer {
	position: absolute;
	left: 0;
	top: 0;
	}
.leaflet-container {
	overflow: hidden;
	}
.leaflet-tile,
.leaflet-marker-icon,
.leaflet-marker-shadow {
	-webkit-user-select: none;
	   -moz-user-select: none;
	        user-select: none;
	  -webkit-user-drag: none;
	}
/* Safari renders non-retina tile on retina better with this, but Chrome is worse */
.leaflet-safari .leaflet-tile {
	image-rendering: -webkit-optimize-contrast;
	}
/* hack that prevents hw layers "stretching" when loading new tiles */
.leaflet-safari .leaflet-tile-container {
	width: 1600px;
	height: 1600px;
	-webkit-transform-origin: 0 0;
	}
.leaflet-marker-icon,
.leaflet-marker-shadow {
	display: block;
	}
/* .leaflet-container svg: reset svg max-width decleration shipped in Joomla! (joomla.org) 3.x */
/* .leaflet-container img: map is broken in FF if you have max-width: 100% on tiles */
.leaflet-container .leaflet-overlay-pane svg,
.leaflet-container .leaflet-marker-pane img,
.leaflet-container .leaflet-shadow-pane img,
.leaflet-container .leaflet-tile-pane img,
